.enterpriseDashboards {
  padding: 24px;
}

@media (min-width: 992px) {
  .enterpriseDashboards {
    padding: 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.links {
  margin: -8px;
  font-size: 0;
}

.link {
  display: inline-block;
  margin: 8px;
  padding: 4px 16px;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  text-overflow: ellipsis;
  font-size: 14px;
  border: 1px solid rgb(239, 51, 106);
  border-radius: 4px;
  &:hover {
    text-decoration: none;
  }
  &Active {
    background-color: rgb(239, 51, 106);
    color: #fdfdfd;
  }
}

.content {
  margin-top: 32px;
}
